import { createApp } from 'vue'
import App from './App.vue'
import store from './store'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faCloudSun, faCloud, faCloudShowersHeavy, faPlay, faHeart, faPause, faStepBackward, faStepForward } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faCloudSun, faCloud, faCloudShowersHeavy, faPlay, faHeart, faPause, faStepBackward, faStepForward)


createApp(App).use(store).component('font-awesome-icon', FontAwesomeIcon).mount('#app')

