<style scope lang="scss">
    .select-box{
        width: 112px;
        height: 62px;
        position: relative;
        margin-bottom: 10px;
        select{
            appearance: none;
            border: 0px;
            padding: 10px 20px 10px 0px;
            letter-spacing: 0.5px;
            font-size: 30px !important;
            font-weight: 500;
            position: relative;
            outline: none;
            background-color: #f5f5f5;
            option{
                font-size: 12px !important;
            }
        }
        &:after{
            content: "";
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 5px 5px 0 5px;
            border-color: #000000 transparent transparent transparent;
            position: absolute;
            right: 0px;
            bottom: 20px;
            pointer-events: none;
        }
    }

    .weather-temp{
        font-size: 14px;
    }

    .weather-describe{
        font-size: 16px;
        letter-spacing: 0.5px;
    }



</style>

<template>
    <div class="select-box">
        <select v-model="weatherData.select">
            <option v-for="item in weatherData.data" :key="item.location" class="option-city">
                {{item.location}}
            </option>
        </select>
    </div>

    <div v-if="selectCityData.data.location">
        <font-awesome-icon  icon="cloud-sun" v-if="selectCityData.data.weather.value < 3" />
        <font-awesome-icon  icon="cloud" v-if="selectCityData.data.weather.value < 8 && selectCityData.data.weather.value > 2 " />
        <font-awesome-icon  icon="cloud-showers-heavy" v-if="selectCityData.data.weather.value > 7" />
        <p class="weather-describe">{{selectCityData.data.weather.describe}}</p>
        <p class="weather-temp">最高 {{selectCityData.data.temp.maxT}}°&emsp;最低 {{selectCityData.data.temp.minT}}°</p>
    </div>
    
</template>

<script>

import axios from 'axios';
import {  onMounted, reactive, watch } from 'vue';
export default {
    setup(){
        
        const weatherData = reactive({select: '臺北市',data: []});
        const selectCityData = reactive({ data:'' });        

        watch(
            ()=>weatherData.select,
            (selectCity)=>{
            selectCityData.data = weatherData.data.find(city => city.location == selectCity);
            // console.log(selectCityData);
            // 監聽選項改變時，選中資料更新
        });      
        
        // console.log(weatherData.select);

        onMounted(()=>{
            axios
            .get('https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=CWB-3B5D59AD-C600-4D31-A81C-008FD7B160D1&format=JSON')
            .then((res)=>{
                // console.log(res.data.records.location);
                const weatherRes = res.data.records.location;
                weatherRes.forEach((item) => {
                    weatherData.data.push({
                        location: item.locationName,
                        weather: {
                            describe: item.weatherElement[0].time[0].parameter.parameterName,
                            value: item.weatherElement[0].time[0].parameter.parameterValue },
                        temp: {
                            minT: `${item.weatherElement[2].time[0].parameter.parameterName}`,
                            maxT: `${item.weatherElement[4].time[0].parameter.parameterName}`
                        }
                    });
                });
                 //取得天氣api資料，並整理格式

                selectCityData.data = weatherData.data.find(city => city.location == weatherData.select);
                // console.log(selectCityData.data);
                //讓選中資料區有預設資料
            
            });
        });

        return{ weatherData, selectCityData};

    }

}

</script>


