<style lang="scss" scoped>   



    .time-moment{
        font-size: 48px;
        font-weight: 600;
        text-align: right;
        letter-spacing: 2px;
        span{
            font-size: 16px;
            font-weight: 400;
            display: block;
            letter-spacing: 0px;
            margin-bottom: -15px;
            margin-top: 15px;
        }
    }

</style>

<template>

<div class="time"></div>

<div class="time-moment">
    <span>{{todayDate}}</span>
    {{todayMoment}}
</div>
  
</template>

<script>
import { ref } from 'vue';
    export default {
        setup(){

            const todayDate = ref('');
            const todayMoment = ref('');

            setInterval(() => {
                let today = new Date();
                let year = today.getFullYear();
                let month = today.getMonth()+1 >= 10 ? today.getMonth()+1 : `0${today.getMonth()+1}`;
                let day = today.getDate() >= 10 ? today.getDate() : `0${today.getDate()}`;
                let hour = today.getHours() >= 10 ? today.getHours() : `0${today.getHours()}`;
                let minute = today.getMinutes() >= 10 ? today.getMinutes() : `0${today.getMinutes()}`;
                let second = today.getSeconds() >= 10 ? today.getSeconds() : `0${today.getSeconds()}`;
                todayDate.value = `${year}年 ${month}月 ${day}日`;
                todayMoment.value = `${hour}:${minute}:${second}`;
                //取得現在時間
            },1000)
            

            return{ todayDate,todayMoment };
        }
        

    }
</script>