<script>
import { computed, reactive } from '@vue/runtime-core';

export default {
    props: {
        playlistData:{
            type: Object,
            default: () => ({}),
        },
        handleListPlay:{
            type: Function,
            default: () => {},
        },
        handleListPause:{
            type: Function,
            default: () => {},
        },
    },
    setup( props ){
        
        const playlistData = reactive({ data:'' }); 
        playlistData.data = computed(() => props.playlistData.data);

        const timeFormat = (time) => {
            const minute = parseInt(time / 60) || 0;
            const second = parseInt(time) % 60 || 0;
            return `${minute}:${second > 9 ? second : '0' + second}`;
            //秒數轉換格式
        };

        return{ props, timeFormat, playlistData};
    }

}
</script>

<template>
<h2>歌曲清單</h2>

<ul class="nav nav-pills">
  <li class="nav-item" v-if="playlistData.data">
    <a class="nav-link active" data-toggle="pill" href="#playlist01" >
        {{playlistData.data[0][0].album}}
    </a>
  </li>
  <li class="nav-item" v-if="playlistData.data">
    <a class="nav-link" data-toggle="pill" href="#playlist02">
        {{playlistData.data[1][0].album}}
    </a>
  </li>
</ul>

<div class="tab-content">
    <div class="tab-pane fade show active" id="playlist01">
    <table class="table table-hover">
        <tbody>
            <tr v-for="(music, index) in playlistData.data[0]" :key="music.title">
            <td width="10%" style="text-align: center;">{{index + 1}}</td>
            <td width="5%"><img :src="music.coverImg"></td>
            <td width="55%" class="song-title">
                {{music.title}}<br>
                <small>{{music.artist}}</small>
            </td>
            <td width="15%">{{timeFormat(music.duration)}}</td>
            <td width="10%">
                <div class="div" v-show="!music.playing" @click="props.handleListPlay(music, index)">
                   <font-awesome-icon icon="play" /> 
                </div>
                <div class="div" v-show="music.playing" @click="props.handleListPause(music)">
                   <font-awesome-icon icon="pause" /> 
                </div>
            </td>
            </tr>
        </tbody>
    </table>   
    </div>

    <div class="tab-pane fade" id="playlist02">
    <table class="table table-hover">
        <tbody>
            <tr v-for="(music, index) in playlistData.data[1]" :key="music.title">
            <td width="10%" style="text-align: center;">{{index + 1}}</td>
            <td width="5%"><img :src="music.coverImg"></td>
            <td width="55%" class="song-title">
                {{music.title}}<br>
                <small>{{music.artist}}</small>
            </td>
            <td width="15%">{{timeFormat(music.duration)}}</td>
            <td width="10%">
                <div class="div" v-show="!music.playing" @click="props.handleListPlay(music, index)">
                   <font-awesome-icon icon="play" /> 
                </div>
                <div class="div" v-show="music.playing" @click="props.handleListPause(music)">
                   <font-awesome-icon icon="pause" /> 
                </div>
            </td>
            </tr>
        </tbody>
    </table>  
    </div>
</div>




</template>

