<script>
import { ref, reactive, computed } from 'vue';
export default {
    props: {
        nowMusic: {
            type: Object,
            default: () => ({}),
        },
        currentTime: {
            type: Number,
            default: 0,
        },
        handleProgress: {
            type: Function,
            default: () => {},
        },
        handlePlayerPlay: {
            type: Function,
            default: () => {},
        },
        handlePlayerPause: {
            type: Function,
            default: () => {},
        },
        handleNext: {
            type: Function,
            default: () => {},
        },
        handlePrev: {
            type: Function,
            default: () => {},
        }
    },

    setup(props){

        const currentTime = ref();
        currentTime.value = computed(() => props.currentTime);

        const nowMusic = reactive({data:''});
        nowMusic.data = computed(() => props.nowMusic.data);
        

        const timeFormat = (time) => {
            const minute = parseInt(time / 60) || 0;
            const second = parseInt(time) % 60 || 0;
            return `${minute}:${second > 9 ? second : '0' + second}`;
            //秒數轉換格式
        };


        
        return{ props, timeFormat, nowMusic };
    }

}
</script>

<template>
<div :class="['imgbox',{'active': nowMusic.data.playing}]">
    <img :src="nowMusic.data.coverImg">
</div>
<h1>{{nowMusic.data.title}}</h1>
<h3>{{nowMusic.data.artist}}</h3>

<div class="progress-box">
    <div class="progress-time">{{timeFormat(currentTime)}}</div>
    <input type="range"  
    v-model="currentTime" 
    :max="nowMusic.data.duration" 
    @click="props.handleProgress(currentTime)" 
    @mouseup="props.handleProgress(currentTime)"
    >    
    <div class="progress-time">{{timeFormat(nowMusic.data.duration)}}</div>
</div>

<div class="control-box">
    <font-awesome-icon icon="step-backward" size="lg" @click="props.handlePrev"/> 
    <div class="play-btn" v-show="!nowMusic.data.playing" @click="props.handlePlayerPlay">
        <font-awesome-icon icon="play" size="lg" color="#fff"/> 
    </div>
    <div class="play-btn" v-show="nowMusic.data.playing" @click="props.handlePlayerPause">
        <font-awesome-icon icon="pause" size="lg" color="#fff"/> 
    </div>
    <font-awesome-icon icon="step-forward" size="lg" @click="props.handleNext"/> 
</div>




  
</template>

