<script>
import axios from 'axios';
import { computed, onMounted, reactive, ref} from 'vue';


import WeatherBox from '@/components/WeatherBox.vue';
import TimeBox from '@/components/TimeBox.vue';
import PlaylistBox from '@/components/PlaylistBox.vue';
import MusicPlayer from '@/components/MusicPlayer.vue'

export default {
  name: 'App',
  components: {
    WeatherBox,
    TimeBox,
    PlaylistBox,
    MusicPlayer,
  },

  setup(){

    const playlistData = reactive({ data: '' });
    const isPlayerLoad = ref(false);
    const nowPlaylistIndex = ref(0);
    let nowPlaylistId = reactive(['XUPoYnbVwNM', "W8NOum-kh-I", "glK5DW9070w", "3CGnT7NbCmw"]);
    let nowPlaylist = reactive([]);
    const prevMusic = reactive({data: ''});
    const nowMusic = reactive({data: ''});
    const currentTime = ref();

    //==============================================================
    //==============================================================

    const tag = document.createElement('script');
    tag.src = "https://www.youtube.com/iframe_api";
    const firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
    let player = null;
    window.onYouTubeIframeAPIReady = function(){
      player = new YT.Player('player', {
        height: '0',
        width: '0',
        videoId: '',
        playerVars: {
          listType: 'playlist', 
          playlist: [],
          loop: 1,
        },
        events: {
          'onReady': onPlayerReady,
          'onStateChange': onPlayerStateChange, 
        },
      });
    };
    
    function onPlayerReady() {
      isPlayerLoad.value = true;
      player.cuePlaylist(nowPlaylistId);
      player.pauseVideo();
      nowMusic.data = playlistData.data[0][0];
      getPlaytime();
    }

    function onPlayerStateChange(event) {
      if (event.data == YT.PlayerState.ENDED) {
        let index = player.getPlaylistIndex();
        let length = nowPlaylist.length;
        console.log(index);
        //得知下個的序號
        if(index+1 == length){
          nowMusic.data.playing = false;
          nowPlaylist[0].playing = true;
          nowMusic.data = nowPlaylist[0];
          player.playVideoAt(0);
        }else{
          nowMusic.data.playing = false;
          nowPlaylist[index].playing = true;
          nowMusic.data = nowPlaylist[index];
          
        }
      }    
    }

    //==============================================================
    //==============================================================

    function handleListPlay(music, index){
      nowMusic.data = music;

      nowPlaylistIndex.value = playlistData.data.findIndex( arr => arr.indexOf(music) !== -1 );
      nowPlaylist = playlistData.data[nowPlaylistIndex.value];
      nowPlaylistId = nowPlaylist.map(el => el.videoId);
      
      //先找出點擊目標所在清單，並塞入播放id列表
    
      if(music !== prevMusic.data){
        player.loadPlaylist(nowPlaylistId, index);
        prevMusic.data = music;
      }else{
        player.playVideo();
      };
      //判斷前歌曲跟點擊目標不一樣

      playlistData.data.forEach((arr) => {
        arr.forEach(el => el.playing = false);
      });
      music.playing = true;
      //先false所有,再打開對象true
    }

    const handleListPause =  (music) => {
      player.pauseVideo();
      music.playing = false;
    }

    //==============================================================
    //==============================================================

    function getPlaytime(){
      setInterval(()=>{
        currentTime.value = Math.floor(player.getCurrentTime());
      },1000)
    };
    //取得現在播放時間

    const handlePlayerPlay = () => {
      player.playVideo();
      nowMusic.data.playing = true;
    }

    const handlePlayerPause = () => {
      player.pauseVideo();
      nowMusic.data.playing = false;
    }

    const handleProgress = (time) => {
      if(player.getPlayerState() == 5){
        player.pauseVideo().seekTo(time);
      }else{
        player.seekTo(time);
      }
    }

    const handleNext = () => {
      let index = player.getPlaylistIndex() + 1;
      //得知下個的序號
      let length = nowPlaylist.length;

      if(index == length){
        nowMusic.data.playing = false;
        nowPlaylist[0].playing = true;
        nowMusic.data = nowPlaylist[0];
        player.playVideoAt(0);
      }else{
        nowMusic.data.playing = false;
        nowPlaylist[index].playing = true;
        nowMusic.data = nowPlaylist[index];
        player.nextVideo();
      }
    }

    const handlePrev = () => {
      let index = player.getPlaylistIndex() - 1;
      //得知下個的序號
      let length = nowPlaylist.length;

      if(index < 0){
        nowMusic.data.playing = false;
        nowPlaylist[length-1].playing = true;
        nowMusic.data = nowPlaylist[length-1];
        player.playVideoAt(length-1);
      }else{
        nowMusic.data.playing = false;
        nowPlaylist[index].playing = true;
        nowMusic.data = nowPlaylist[index];
        player.previousVideo();
      }
    }



  
    onMounted(()=>{
      axios
      .get('./json/playlist.json')
      .then((res) => {
        playlistData.data = res.data;    
        nowPlaylist = playlistData.data[nowPlaylistIndex.value];

        })

    });
    
    return{ 
            playlistData,
            isPlayerLoad,
            handleListPlay,
            handleListPause,
            nowMusic,
            handlePlayerPlay,
            handlePlayerPause,
            handleProgress,
            currentTime,
            handleNext,
            handlePrev,
          };
  }

  
}
</script>

<template>
<div class="container">
  <div class="row">
    <div class="col-md-6">

      <div class="row">
        <div class="col-md-6">
          <WeatherBox/>
        </div>
        <div class="col-md-6">
          <TimeBox/>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12">
          <PlaylistBox v-if="isPlayerLoad"
            :playlistData="playlistData"
            :handleListPlay="handleListPlay"
            :handleListPause="handleListPause"
          />
        </div>
      </div>
    </div>

    <div class="col-sm-6">
      <div id="player"></div>
      <MusicPlayer  v-if="nowMusic.data"
        :nowMusic="nowMusic"
        :currentTime="currentTime"
        :handleProgress="handleProgress"
        :handlePlayerPlay="handlePlayerPlay"
        :handlePlayerPause="handlePlayerPause"
        :handleNext="handleNext"
        :handlePrev="handlePrev"
      />
    </div>
    
  </div>
</div>

</template>

